import React from 'react';

interface BtnProps {
  id?: string;
  onClick?: () => void;
  children?: React.ReactNode;
}

const Btn = React.memo((props: BtnProps) => {
  return (
    <button id={props.id} onClick={props.onClick}>
      {props.children}
    </button>
  );
});

export default Btn;
